.posts {
	max-width: 50rem;
	padding: 0 3.8rem 0 1rem;

	@include bp ($point_2) {
		padding-right: 3.8rem;
	}

	@include bp ($point_3) {
		padding-right: 2rem;
	}

	@include bp ($point_6) {
		padding-right: 1rem;
	}
}

.posts__title {
	margin-bottom: 2.2rem;

	font-size: 2rem;
	font-weight: 300;
	line-height: 2.8rem;
	letter-spacing: -0.4px;
	color: #22252E;

	@include bp ($point_5) {
		margin-bottom: 2.4rem;

		font-size: 1.8rem;
		text-align: center;
	}
}

.posts__list {
	margin-bottom: 2.4rem;

	@include bp ($point_3) {
		margin-bottom: 3rem;
	}

	@include bp ($point_6) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.posts__item {
	@extend %global_flex_block_row_nowrap_flex-start;

	padding-bottom: 1.8rem;
	margin-bottom: 1.8rem;

	border-bottom: 1px solid #EBECED;

	@include bp ($point_5) {
		@include flex_block(column);

		padding-bottom: 0.8rem;
		margin-bottom: 1rem;
	}
}

.posts__item__last {
	margin-bottom: 0;
}

.posts__item__bottom {
	padding-bottom: 3.6rem;

	@include bp ($point_3) {
		padding-bottom: 0.8rem;
	}
}

.posts__item__link {
	display: block;

	@include bp ($point_5) {
		width: 100%;

		text-align: center;
	}
}

.posts__item__img {
	display: block;
	min-width: 17.6rem;
	min-height: 11.4rem;
	margin-right: 2.6rem;

	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	overflow: hidden;

	@include bp ($point_2) {
		width: 12.6rem;
		height: 8.8rem;
		margin-right: 1.2rem;
	}

	@include bp ($point_3) {
		min-width: 8.2rem;
		max-width: 8.2rem;
		background-size: cover;
		height: 5rem;
	}

	@include bp ($point_5) {
		min-width: 100%;
		max-width: 100%;
		margin-bottom: 1rem;
		object-fit: contain;
	}
}



.posts__content__link {
	display: block;
	margin-bottom: 0.6rem;

	font-size: 1.5rem;
	line-height: 1.8rem;
	letter-spacing: -0.2px;
	font-weight: 300;
	color: #000;

	@include bp ($point_1) {
		font-size: 1.2rem;
		line-height: 1.8rem;
	}

	@include bp ($point_5) {
		font-size: 1.2rem;
		line-height: 1.4rem;
		text-align: center;
	}

	@include bp ($point_6) {
		max-width: 80%;
		margin: 0 auto 0.2rem;

		font-size: 1rem;
		line-height: 1.2rem;
	}
}

.posts__inner__date {
	font-size: .8rem;
	margin-bottom: .6rem;

	@include bp ($point_5) {
		font-size: .6rem;
		text-align: right;
	}
}

.posts__autor {
	display: inline-block;
	margin-right: 5px;

	color: #323642;

	@include bp ($point_5) {
		margin-right: 2px;
	}
}

.posts__date {
	color: #889099;
}

.posts__tags__list {
	@extend %global_flex_block_row_wrap_flex-start;

	margin-bottom: 1rem;

	font-size: .8rem;
	letter-spacing: 0.45px;
	text-transform: uppercase;
	list-style: none;

	@include bp ($point_5) {
		@include flex_block(row, wrap, center);
	}
}

.posts__tags__item {
	color: #4CB649;
	padding-right: .6rem;
}

.posts__tags__link {
	position: relative;

	color: #4CB649;

	transition: text-shadow 0.2s, color 0.2s;

	&:hover {
		color: #fff;
		text-shadow: 0 0 .2rem #204b1f;
	}

	&::after {
		content: ',';
		position: absolute;
		bottom: 0px;
		right: -3px;
		color: #4CB649;
		font-size: .8rem;
	}
}

.posts__coments {
	position: relative;

	display: inline-block;
	padding-right: 1.2rem;

	font-size: .8rem;
	color: #889099;

	border-radius: .6rem;

	transition: color 0.2s;

	@include bp ($point_5) {
		display: block;

		font-size: 12px;
		text-align: right;
	}

	&::after {
		content: '';
		position: absolute;
		top: -2px;
		right: -2px;

		height: 1.2rem;
		width: 1.2rem;

		background-image: url(../images/icons/coment.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
	}

	&:hover {
		color: #5079a8;
	}
}

.posts__btn {
	@extend %posts__btn;
}