%posts__btn {
	display: block;

	margin: 0 auto;
	padding: 0 4rem 0;

	color: #4CB649;
	font-size: .8rem;
	letter-spacing: .3px;
	line-height: 2rem;
	text-transform: uppercase;

	background-color: #fff;
	border: 1px solid #4CB649;
	border-radius: 1.2rem;

	cursor: pointer;

	transition: box-shadow 0.2s, color 0.2s, background-color 0.2s, border-color 0.2s;

	@include bp ($point_5) {
		width: 100%;
		margin-bottom: 1.8rem;
	}

	&:hover {
		color: #81ec7e;
		border-color: #81ec7e;
		background-color: #539c51e0;
	}

	&:active {
		color: #fff;
		background-color: #2e572d;
	}
}