//------------------------------------------------------------layout
.header {
	font-family: 'Roboto', sans-serif;
	font-size: .8rem;
	line-height: 1rem;
	letter-spacing: .06rem;
	text-transform: uppercase;
	padding-top: 2.6rem;
	margin-bottom: 2.8rem;

	@include bp ($point_3) {
		margin-bottom: 1.6rem;
		padding-top: 1.8rem;
	}
}

.header__inner {
	@include flex_block(row, wrap, space-between, center);
	padding: 1.4rem 2rem 1.2rem 2rem;

	background-color: #F7F7F7;

	@include bp ($point_3) {
		padding: .6rem;
	}

	@include bp ($point_5) {
		justify-content: center;
	}
}

.header__inner__tags {
	@include flex_block(row, wrap, flex-start);

	@include bp ($point_5) {
		display: none;
	}
}

.header__tags__title {
	display: inline-block;
	margin-right: 2.8rem;

	color: #323642;

	@include bp ($point_2) {
		margin-right: 1rem;
	}
}

.header__list {
	@extend %global_flex_block_row_wrap_flex-start;
	margin: 0 -1.4rem -1rem 0;
}

.header__item {
	padding: 0 1.4rem 1rem 0;

	@include bp ($point_2) {
		padding: 0 .6rem .4rem 0;
	}

}

.header__nav {
	@include bp ($point_5) {
		width: 100%;
	}
}

.header__tags__link {
	color: #4CB649;
	transition: text-shadow 0.3s, color 0.3s;

	&:hover {
		color: #fff;
		text-shadow: 0 0 .6rem #2a6428;
	}
}

.header__nav__list {
	@extend %global_flex_block_row_wrap_flex-end;

	@include bp ($point_5) {
		@include flex_block(row, wrap, space-around);
		width: 100%;
	}
}

.header__nav__item {
	display: inline-block;

	&:not(:last-child) {
		padding: 0px 2.6rem .6rem 0px;

		@include bp ($point_2) {
			padding: 0px 1.2rem .4rem 0px;
		}
	}
}

.header__nav__link {
	color: #323642;
	transition: text-shadow 0.3s, color 0.3s;

	&:hover {
		text-shadow: 0 0 .8rem #000;
		color: #fff;
	}

	&:active {
		color: rgb(255, 0, 0);
		text-shadow: 0 0 .8rem rgba(255, 0, 0, 0.664);
	}

}


//------------------------------------------------------------layout###